@font-face {
  font-family: 'JetBrainsBold';
  src: url('/static/JetBrainsMono-ExtraBold.ttf'),
    url('/static/JetBrainsMono-ExtraBold.ttf') format('truetype');
}

@font-face {
  font-family: 'JetBrainsRegular';
  src: url('/static/JetBrainsMono-Regular.ttf'),
    url('/static/JetBrainsMono-Regular.ttf') format('truetype');
}

:root {
  font-size: 16px;
  cursor: none;
}

@media (max-width: 850px) {
  :root {
    font-size: 14px;
    cursor: none;
  }
}

::selection {
  color: #fff;
  background-color: #e64a19;
}

html,
body {
  font-family: 'JetBrainsMono-Regular', monospace;
}

@media (min-width: 841px) and (max-width: 1284px) {
  :root {
    font-size: 80%;
  }
}

@media (min-width: 800px) and (max-width: 840px) {
  :root {
    font-size: 45%;
  }
}
@media (min-width: 300px) and (max-width: 800px) {
  :root {
    font-size: 48%;
  }
}

a {
  cursor: pointer;
}
